/* dropdown menu animation */
/* https://codinhood.com/micro/10-dropdown-menu-animations-css-transform */
@import '../../styles/main.scss';

#pageHead {
  background-color: $colorLight;
  display: flex;
  align-items: center;
  @include xs {
    flex-direction: column;
    justify-content: center;
    padding: 1rem 0 0 0;
  }
  @include sm {
    padding: 1rem;
    flex-direction: row;
    justify-content: space-between;
  }
}

#meTitle {
  display: flex;
  align-items: center;
  font-family: $fontFancy;
  font-size: 3rem;
  color: $colorMid;
  // padding: 1rem;
  @include xs {
    flex-direction: column;
    padding-bottom: 0.5rem;
  }
  @include sm {
    flex-direction: row;
  }
}

#icon {
  @include xs {
    padding-bottom: .25rem;
  }
  @include sm {
    padding-right: 1rem;
  }
}